html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  color: #292929;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1429px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  padding: 18px 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo__svg {
  width: 79px;
  height: 23px;
  fill: #01996d;
}

.nav__list_dir_row {
  display: flex;
  margin: 0 100px 0 0;
}

.nav__list_dir_col {
  flex-direction: column;
}

.nav__item_dir_row:not(:last-child) {
  margin: 0 70px 0 0;
}

.nav__item_dir_col:not(:first-child) {
  margin: 70px 0;
}

.nav__link {
  font-weight: 500;
  line-height: 24px;
  transition: all 0.2s;
}

.nav__link_color_white {
  font-size: 25px;
  color: #fff;
}

.nav__link:hover {
  color: #01996d;
}

.auth-list__item {
  display: inline-block;
}

.auth-list__item:first-child {
  margin: 0 36px 0 0;
}

.auth-list__link {
  line-height: 24px;
}

.auth-list__link:hover {
  color: #01996d;
}

.btn {
  display: block;
  border: 1px solid #01996d;
  border-radius: 4px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #01996d;
  transition: all 0.2s;
}

.btn_size_s {
  width: 183px;
  height: 51px;
  line-height: 51px;
}

.btn_size_m {
  width: 227px;
  height: 65px;
  line-height: 65px;
}

.btn:hover {
  border-color: #01996d;
  color: #01996d;
  background-color: transparent;
}

.hero {
  padding: 174px 0 335px;
  background-image: url("../img/hero__bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__title {
  max-width: 440px;
}

.hero__parag {
  margin-top: 34px;
  max-width: 420px;
  line-height: 28px;
}

.hero__btn {
  margin-top: 53px;
}

.title {
  font-family: "Georgia", sans-serif;
  font-weight: 700;
  font-size: 65px;
  line-height: 74px;
}

.title_color_main {
  color: #01996d;
}

.title_color_white {
  color: #fff;
}

.title_size_s {
  font-size: 50px;
}

.title_size_m {
  font-size: 65px;
}

.steps {
  padding: 210px 0 30px;
}

.steps__text {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.steps__overhead {
  left: 0;
  top: -33px;
}

.steps__title {
  max-width: 580px;
}

.steps__parag {
  max-width: 670px;
  font-weight: 500;
  line-height: 30px;
}

.overhead {
  position: absolute;
  font-size: 24px;
}

.cards {
  margin: 195px 0 0;
}

.cards__list {
  display: flex;
  justify-content: space-between;
}

.cards__item {
  position: relative;
  max-width: 390px;
}

.cards__item:nth-child(2) {
  margin: 290px 50px 0;
}

.cards__item:nth-child(3) {
  margin: 50px 0 0;
}

.cards__num {
  position: absolute;
  left: 0;
  top: -111px;
  font-family: "Georgia", sans-serif;
  font-size: 150px;
  text-align: center;
  color: rgba(1, 153, 109, 0.2);
}

.cards__title {
  font-family: "Georgia", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-align: center;
  color: #000;
}

.cards__parag {
  margin: 65px 0 0;
  line-height: 30px;
}

.cards__img {
  margin: 53px 0 0;
  border-radius: 12px;
  height: 492px;
  max-width: 390px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.cards__img_num_01 {
  background-image: url("../img/cards__img1.jpg");
}

.cards__img_num_02 {
  background-image: url("../img/cards__img2.jpg");
}

.cards__img_num_03 {
  background-image: url("../img/cards__img3.jpg");
}

.video {
  padding: 230px 0 175px;
  background-color: #245852;
}

.video__container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 30px;
}

.video__text {
  margin: 0 37px 0 0;
  max-width: 580px;
}

.video__parag {
  margin: 31px 0 0;
  font-size: 25px;
  color: #fff;
}

.video__btn {
  margin: 75px 0 0;
}

.play {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  width: 890px;
  height: 516px;
  background-image: url("../img/video__img.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.play::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 16px;
  background-color: rgba(0, 0, 0, 0.2);
}

.play__button {
  position: relative;
  z-index: 1;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 80px;
  height: 80px;
}

.play__svg {
  position: absolute;
  left: 54%;
  top: 50%;
  margin-right: -50%;
  width: 26px;
  height: 33px;
  fill: #fff;
  transform: translate(-50%, -50%);
}

.product {
  padding: 120px 0 244px;
}

.product__container {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.product__img {
  margin: 0 30px 0 60px;
  border-radius: 18px;
  width: 638px;
  height: 573px;
  background-image: url("../img/product__img.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.product__text {
  max-width: 584px;
}

.product__overhead {
  margin: 9px 0 0;
}

.product__title {
  margin: 52px 0 0;
  line-height: 78px;
}

.product__parag {
  margin: 26px 0 0;
  max-width: 420px;
  line-height: 26px;
}

.product__btn {
  margin: 37px 0 0;
}

.inf {
  position: absolute;
  left: 30px;
  bottom: -43px;
  border-radius: 3px;
  padding: 28px 63px;
  text-align: center;
  color: #000;
  background-color: #fff;
  filter: drop-shadow(0 3.62249px 18.1125px rgba(165, 165, 165, 0.25));
}

.inf__title {
  position: relative;
  font-weight: 400;
  font-size: 14px;
}

.inf__title::before {
  content: "";
  position: absolute;
  left: 28px;
  bottom: -10px;
  width: 110px;
  height: 1px;
  background-color: #000;
}

.inf__ava {
  margin: 20px auto 0;
  width: 57px;
  height: 57px;
  background-image: url("../img/product__ava.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.inf__name {
  font-size: 11px;
}

.inf__desc {
  margin: 20px 0 0;
  font-family: "Georgia", sans-serif;
  font-weight: 700;
  font-size: 15px;
}

.inf__list {
  list-style-type: disc;
  margin: 15px 0 0;
}

.inf__item {
  font-size: 10px;
  text-align: left;
}

.inf__item:last-child {
  margin: 15px 0 0;
}

.footer {
  padding: 110px 0;
  background-color: #245852;
}

.footer__logo {
  font-family: "AvenirLT", sans-serif;
  font-weight: 700;
  font-size: 40px;
}

@media (max-width: 1350px) {
  .steps__text {
    flex-direction: column;
    text-align: center;
  }

  .steps__parag {
    margin: 20px 0 0;
  }

  .steps {
    padding: 110px 0 30px;
  }

  .overhead {
    position: static;
  }

  .cards__item:nth-child(2) {
    margin: 180px 50px 0;
  }

  .cards__img {
    height: 300px;
  }

  .cards {
    margin: 120px 0 0;
  }
}

@media (max-width: 1140px) {
  .header__nav {
    display: none;
  }

  .auth-list {
    display: none;
  }

  .cards__item:nth-child(2) {
    margin: 0 50px;
  }

  .cards__item:nth-child(3) {
    margin: 0;
  }

  .cards__item {
    max-width: 250px;
    text-align: center;
  }

  .cards__num {
    top: -70px;
    font-size: 100px;
  }

  .cards__parag {
    margin: 30px 0 0;
  }

  .cards__img {
    display: none;
  }

  .video__container {
    flex-direction: column;
  }

  .video__text {
    margin: 0;
  }

  .video__btn {
    margin: 40px auto;
  }

  .video {
    padding: 110px 0 30px;
    text-align: center;
  }

  .play {
    height: 300px;
  }

  .product__text {
    max-width: 400px;
  }

  .product__title {
    margin: 0;
    line-height: 68px;
  }
}

@media (max-width: 900px) {
  .product__container {
    justify-content: center;
    text-align: center;
  }

  .product__img {
    display: none;
  }

  .product__text {
    max-width: 100%;
  }

  .product__parag {
    max-width: 100%;
  }

  .product__btn {
    margin: 30px auto 0;
  }

  .product {
    padding: 80px 0 344px;
  }

  .inf {
    left: 50%;
    top: 33%;
    margin-right: -50%;
    transform: translate(-50%, 98%);
  }
}

@media (max-width: 650px) {
  body {
    font-size: 16px;
  }

  .container {
    padding: 0 15px;
  }

  .btn_size_m {
    width: 180px;
    height: 50px;
    line-height: 50px;
  }

  .hero__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .hero__parag {
    margin-top: 20px;
    line-height: 22px;
  }

  .hero__btn {
    margin-top: 40px;
  }

  .title_size_s {
    font-size: 35px;
    line-height: 45px;
  }

  .title_size_m {
    font-size: 50px;
    line-height: 60px;
  }

  .cards__list {
    flex-direction: column;
    align-items: center;
  }

  .cards__item:nth-child(2) {
    margin: 0;
  }

  .cards__item {
    max-width: 100%;
    text-align: left;
  }

  .cards__item:not(:first-child) {
    margin: 80px 0 0;
  }

  .cards__title {
    text-align: left;
  }

  .cards__parag {
    margin: 10px 0 0;
  }

  .video__container {
    padding: 0 15px;
  }

  .inf {
    top: 21%;
    transform: translate(-50%, 105%);
  }

  .footer__nav {
    text-align: center;
  }
}

@media (max-width: 437px) {
  .inf {
    top: 39%;
    transform: translate(-50%, 95%);
  }
}